.app__navbar {
    width: 100%;

    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 1rem 2rem;
    background-color: #cdf8f886;
    backdrop-filter: blur(4px);
    border: 1px solid #ffffff94;
    position: fixed;
    z-index: 2;
}


.app__navbar-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    img {
        width: 60px;
        height: 60px;

        @media screen and (min-width:2000px) {
            width: 100px;
            height: 100px;
        }
    }
}


.app__navbar-links {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;

    list-style: none;




    li {
        margin: 0 1.7rem;
        cursor: pointer;
    
        flex-direction: column;
    
    
        div {
            width: 5px;
            height: 5px;
            background: transparent;
            border-radius: 50%;
            margin: 0px auto 5px auto;
            margin-bottom: 5px;
        }
    
        a {
            color: var(--gray-color);
            text-decoration: none;
            flex-direction: column;
    
            text-transform: uppercase;
            font-weight: 500;
            font-size: 16px;
    
            transition: all 0.3s ease-in-out;
    
            &:hover {
                color: var(--secondary-color);
            }
        }
    
        &:hover {
            div {
                background-color: var(--secondary-color);
            }
        }
    }

    @media screen and (max-width: 900px) {
        display: none;
    }
}


.app__navbar-menu {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--secondary-color);
    cursor: pointer;

    svg {
        width: 70%;
        height: 70%;
        color: var(--white-color);
     

        &.active {
            color: var(--white-color);
        }
    }

    div {
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: 5;

        padding: 1rem;

        width: 80%;
        height: 100vh;

        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        flex-direction: column;

        background: url('../../assets/bgWhite.png');
        background-color: var(--white-color);
        background-size: cover;
        background-repeat: repeat;

        box-shadow: 0 0 20px rgba(168, 168, 168, 0.15);
        user-select: none;

        svg {
            width: 35px;
            height: 35px;
            color: var(--secondary-color);
        

            &.active {
                color: var(--white-color);
            }
        }

        ul {
            list-style: none;
            margin: 0;
            padding: 0;
            height: 100%;
            width: 100%;

            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;

            li {
                margin: 1rem;

                a {
                    color: var(--gray-color);
                    text-decoration: none;
                    flex-direction: column;
            
                    text-transform: uppercase;
                    font-weight: 500;
                    font-size: 16px;
            
                    transition: all 0.3s ease-in-out;
            
                    &:hover {
                        color: var(--secondary-color);
                    }
                }
            }
        }

    }

    @media screen and (min-width: 900px) {
        display: none;
    }

}






